* {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  color: #fff;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  flex-wrap: nowrap;
}

body {
  background-color: #0a1325;
}

.header {
  width: 100%;
  height: 58px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(to bottom, #081c42, #0f69aa99);
  flex-wrap: nowrap;
}

.left {
  margin: 0 5px 0 30px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.btnLogo {
  cursor: pointer;
  background-color: transparent;
  border: none;
}
.imgLogo {
  width: 130px;
}
.link {
  cursor: pointer;
  text-align: center;
  font-size: 14px;
  padding: 19px;
  margin: 0 10px;
  margin-left: 40px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  border: none;
  background-color: transparent;
}

.right {
  position: relative;
  margin-right: 0 30px 0 5px;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.icons {
  margin: 0 30px;
  padding: 5px;
  border-radius: 40%;
  position: relative;
  top: 3px;
  width: 30px;
}
